-------------------------------------------------------------------
*/

@media (max-width: 767.98px) {
 
    .table-full-sm td, .table-full-sm th {
        padding: 30px 0 !important;
    }

    .timeline {
        grid-template-columns: 1fr 1fr;
    }

    body > header .breadcrumb {
        display: flex;
    }

    .media-box-full .caption h2 {
        font-size: 24px;
        line-height: 45px;
    }

    .media-box-full .caption .extra-field {
        font-size: 13px;
    }

    .slider-zoom-center .glide__slides {
        padding: 0 0 30px 0;
    }

    .slider-zoom-center:not(.slider-phones) .glide__slides .glide__slide--active, .section-map > .container {
        transform: none;
        box-shadow: none;
    }

    .tab-nav.align-center > li a {
        padding: 10px 30px 10px 30px;
    }

    .tab-nav.align-center > li + li a {
        margin: 15px 0 0 0;
    }

    .table-price {
        display: table;
    }

    .table-price thead {
        display: table-header-group;
    }

    .table-price tbody {
        display: table-row-group;
    }


    .table-price tr {
        display: table-row;
    }

    .table-price th, .table-price td {
        display: table-cell;
        padding: 5px;
        overflow: hidden;
        text-overflow: ellipsis;
    }

    .table-grid td > img {
        width: 100%;
        max-width: 150px;
    }

    .section-map > .container {
        border: none;
    }
}
