        grid-template-columns: 1fr;
    }

    h1 {
        font-size: 30px;
        line-height: 45px;
    }

    .btn-icon i {
        left: 18px;
    }

    .btn-icon.btn-sm:not(.btn-anima), .btn-icon.btn-sm.btn-anima:hover {
        padding-left: 55px;
