    .col-lg-6 + .col-lg-6 .margin-23 {
        margin-left: 0px;
    }

    .margin-md-23, .col-lg-6 + .col-lg-6 .margin-md-23 {
        margin: 0 -15px;
        max-width: calc(100% + 35px);
    }

    .text-lg {
        font-size: 40px !important;
        line-height: 60px;
    }
}

