    .counter-big.counter-icon > i {
        display: none;
    }

    body > header h1 {
        font-size: 40px;
    }

    body > header .container > h2 {
        font-size: 13px;
    }

    .themekit-envato {
        display: block;
    }

    .themekit-envato > div:first-child {
        margin-bottom: 30px;
        width: 100%;
    }

    .col-lg-6 form.boxed-area .form-checkbox {
        width: 100%;
        margin-top: 15px;
        margin-bottom: 0;
        padding-left: 0;
        float: none;
        clear: both;
    }

    .btn-store + .btn-store {
        margin-left: 0;
        display: block;
    }

    .col-lg-6 + .col-lg-6 .margin-11, .col-lg-6 .margin-11 {
        margin: 10px 0 0 -5px;
        max-width: calc(100% + 15px);
    }

    .margin-23 {
        margin-left: -10px;
    }

    .cnt-box-testimonials[class*=rating-] > p:not(.testimonial-info):before, .cnt-box-testimonials-bubble[class*=rating-] > .thumb-bar span:first-child:before {
        bottom: -22px;
    }

    .cnt-call i + .caption {
        padding-left: 0;
    }

    .cnt-call > i {
        position: static;
        transform: none;
        margin-bottom: 30px;
    }
}

