.wrapper {
    width: 100%;
    margin: 0px auto;
}

.about {
    background-color: #fff;
}

.about-section_padding {
    padding: 4rem 0 7rem 0;
}

.about .column-left {
    padding-left: 2.5rem;
}

.about .nopadding {
    padding: 0 !important;
    margin: 0 !important;
}

.title-container {
    padding-bottom: 4rem;
    width: 100%;
    float: left;
}

.title-line_left {
    width: 12%;
    height: 2px;
    background-color: #ff971c;
    margin: 15px 88% 0 auto;
}

.title-line {
    width: 12%;
    height: 2px;
    background-color: #ff971c;
    margin: 15px auto 0 auto;
}

.sub-text {
    width: 80%;
    margin: 15px auto 0 auto;
}

.raleway {
    font-family: 'Raleway', sans-serif;
}

.about p {
    color: #727272;
}

.about-section_background-image {
    width: 100%;
    height: auto;
    margin: 0;
    float: left;
    background: url(../../static/media/pattren17.0a10b555.jpg) center center repeat;
}

.title {
    transition: all 0.3s ease-out 0s;
}

.text-box {
    width: 100%;
    float: left;
}

.feature .feature-box {
    width: 100%;
    padding: 0;
    float: left;
}

.feature .feature-box .text-box .shadow {
    box-shadow: 0 20px 20px rgba(0, 0, 0, 0.14);
}

.feature .feature-box .text-box {
    width: 100%;
    float: left;
    padding: 50px;
    background-color: #fff;
    box-shadow: 0 0 0 rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease-out 0s;
}

.feature .feature-box:hover .text-box .title {
    color: #007bff;
}

.feature .feature-box:hover .text-box {
    box-shadow: 1px 4px 20px -2px rgba(0, 0, 0, 0.2);
    transform: translateY(-8px);
}

.feature .feature-box:hover .text-box {
    box-shadow: 0 20px 20px rgba(0, 0, 0, 0.14);
}

.feature p {
    color: #727272;
}

@media only screen and (max-width: 991px) {
    .margin-bottom {
        margin-bottom: 30px;
    }
}

@media (min-width: 576px) {
    .container {
        /* max-width: 0; */
    }
}

@media (min-width: 768px) {
    .sub-text {
        width: 100%;
    }
    .col-md-6 {
        flex: none;
        max-width: none;
    }
    .col-md-4 {
        flex: none;
        max-width: none;
    }
    .about .column-left {
        padding-left: 1rem;
    }
}

@media (min-width: 992px) {
    .col-md-6 {
        flex: 0 0 50%;
        max-width: 50%;
    }
    .col-md-4 {
        flex: 0 0 33.333333%;
        max-width: 33.333333%;
    }
}
.section-main {
    padding: 5rem 0;
}

.section-main h2 {
    font-size: 2.5rem;
    font-weight: 300;
    color: white;
}

.section-main p {
    margin: 2rem 0;
    font-size: 1.2rem;
    color: #ff971c;
}

.section-contact {
    background-color: #ffffff;
}

.section-contact {
    padding: 4rem 0;
}

.section-contact h2 {
    font-size: 3rem;
    font-weight: 600;
    color: #272727;
}

.section-contact p {
    color: #272727;
    margin: 2rem 0 0.8rem 0;
    font-size: 1.3rem;
}

.section-contact .title-line {
    width: 12%;
    height: 2px;
    background-color: #ff971c;
    margin: 0 auto 2rem auto;
}

.form-row {
    width: 100%;
}

.column-half,
.column-full {
    float: left;
    width: 100%;
    padding: 0.65rem;
}

.box {
    margin-bottom: 2rem;
}

span.name-box {
    width: 100% !important;
}

span.email-box {
    width: 100% !important;
}

.box input[type="text"],
.box input[type="email"],
.box textarea {
    width: 100%;
    padding: 15px 20px;
    border: 1px solid #ccc;
    color: rgb(88, 89, 91);
    border-radius: 3px;
    outline: 0;
    transition: all 0.3s ease-out 0s;
}

.box input[type="text"]:focus,
.box input[type="email"]:focus,
.box textarea:focus {
    border: 1px solid #007bff;
    outline: medium none !important;
    transition: all 0.3s ease-out 0s;
}

.text-area {
    height: 9rem;
    font-size: 1rem;
}

.form button {
    margin-top: 1.2rem;
    color: #fff;
    padding: 14px 20px;
    font-size: 14px;
    font-weight: 500;
    background: #ff971c;
    border: 1px solid #ff971c;
    border-radius: 3px;
    font-family: 'Raleway', sans-serif;
    cursor: pointer;
    transition: all 0.3s ease;
}

.form .button:hover {
    background: #454545;
    border: 1px solid #393939;
}

@media only screen and (min-width: 48em) {
    .column-half {
        width: 50%;
    }
}

@media screen and (min-width: 768px) {
    .input-box:not(.message-box):nth-child(3n + 1) {
        clear: left;
    }
    .input-box:not(.message-box):last-child {
        margin-right: 0;
    }
    .input-box:not(.message-box) {
        float: left;
        margin-right: 2.35765%;
        width: 31.76157%;
    }
}


