.toggle-wrapper {
    margin: 0.5em auto;
  }
  
  .toggle-wrapper label {
    padding: 0.3em;
    font-size: 15px;
    display: block;
  }
  
  .toggleContainer {
    width: 100%;
    position: relative;
    background: #ffffff;
    border: 1px solid #eeeeee;
  }
  
  .toggleContainer .toggleOption {
    display: inline-block;
    position: relative;
    z-index: 1;
    text-align: center;
    height: 25px;
    line-height: 20px;
    cursor: pointer;
    transition: all 0.5s ease-in-out;
    color: #807b77;
    font-size: 1em;
  }
  
  .toggleContainer .toggleOption.selected {
    color: #ffffff;
    cursor: auto;
    cursor: initial;
  }
  
  .toggleContainer .toggle {
    position: absolute;
    height: 100%;
    bottom: 0;
    left: 0;
    transition: all 0.4s ease-in-out;
    background: #1BA974;
  }
  
  .optionDisabled {
    background: #e6e6e6;
    cursor: no-drop !important;
  }
